  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  position: absolute;
  left: 0;
  width: 100%;
  overflow: hidden;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
  height: 2.13333rem;
  height: var(--power-scroll-pull-head-height, 2.13333rem);
  color: #969799;
  color: var(--power-scroll-pull-head-text-color, #969799);
